.form {
  align-items: end;
  gap: 10px;
  display: flex;
}

label {
  font-weight: 500;
}

.form-input {
  height: 30px;
  width: 200px;
  color: #2f2f2f;
  background-color: #eeeefc;
  border: 1px solid #6a6ab7;
  border-radius: 2px;
  padding-left: 5px;
  font-size: 16px;
  font-weight: 500;
  display: block;
}

button[type="submit"] {
  height: 30px;
  width: 150px;
  color: #2f2f2f;
  background-color: #babafa;
  border: 1px solid #6a6ab7;
  border-radius: 2px;
  font-size: 16px;
  font-weight: 700;
  transition-property: background-color, border-color, color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

button[type="submit"]:hover, button[type="submit"]:focus {
  color: #fff;
  background-color: #454594;
  border-color: rgba(0, 0, 0, 0);
}

